//Sizes
.icon--small{
  width: 100%;
  height: 100%;

  svg{
    width: 50%;
    height: 50%;
  }
}

.icon--medium{
  width: 100%;
  height: 100%;

  svg{
    width: 100%;
    height: 100%;
  }
}

.icon--large{
  width: 150%;
  height: 150%;

  svg{
    width: 150%;
    height: 150%;
  }
}

.icon--red{
  fill: $brand_danger ;

  svg{
    fill: $brand_danger;
  }

  path{
    fill: $brand_danger ;
  }

}

